// packages / index.js

// 
import ayColor from './ayColor/index'
// 
const components = [
    ayColor
];
//  install 
const install = function (Vue) {
    if (install.installed) return
    install.installed = true
    // 
    components.map(component => {
        console.log(component.name,'<-----i am name of commpoent');
        Vue.component(component.name, component)
    })
}

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
}

export default {
    //  install 
    install,
    // 
    ...components
}